.App {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}

.film-item-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    cursor: pointer;
    border: 2px solid dimgray;
    width: 240px;
    height: 350px;
    margin: 20px 20px 20px 20px;
    font-family: 'Raleway', sans-serif;
    box-shadow: 9px 10px 27px 0px rgba(0,0,0,0.54);
    transition: border-color 0.6s, box-shadow 0.5s ;
}

.film-item-container:hover{
   border-color: white;
    box-shadow: 0 0 0 0 rgba(0,0,0,0.54);
}
.film-image{
    width: 150px;
    height:250px;
    margin-bottom: 10px;
}
.film-title{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    width: 140px;
}
.delete-icon-container{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
}
.delete-film{
    width: 15px;
    height: 15px;
    margin-top: 10px;
    margin-right: 10px;
}

.instrument-panel{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    -webkit-align-self: center;
            align-self: center;
}
.search-input{
    width: 30%;
    margin-left: 10px;
    margin-right: 10px;
}
#sort-button{
    margin-left: 10px;
}
@media (max-width:680px) {
    .instrument-panel{
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: flex-start;
                align-items: flex-start;
    }
    .search-input,
    .upload-input,
    .select-method{
        margin-left: 20px;
        margin-top: 10px;
    }
    #sort-button{
        margin-left: 20px;
        margin-top: 10px;
    }
    .search-input{
        width: 80%;
    }

}
.film-container{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.main-screen-container{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}
.sort-button{
    margin-left: 10px;
}
.film-spin{
    margin-top: 20px;
}

.menu-container{
    width: 100%;
    height: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 20px;
    background: rgb(32, 32, 32);
    font-family: 'Raleway', sans-serif;
}
.menu-list{
    min-width: 380px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    list-style: none;
    text-decoration: none;
    margin-bottom: 0;
    width: 45%;
    color: white;
}
.menu-list > li{
    height: 48px;
    cursor: pointer;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100px;
}
.router-link{
    height: 48px;
    min-width: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-decoration: none;
    color: wheat;
}
.router-link:hover{
    color: rgb(245, 235, 217);
}
.menu-list > li:hover{
    border-bottom: 1px solid rgb(245, 235, 217);
    border-top: 1px solid rgb(245, 235, 217);
}
.logo{
    color: wheat;
}
@media (max-width:680px) {
    .menu-container{
        height: 80px;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .menu-list{
       padding-left: 0;
    }
}
@media (max-width:400px) {
    .menu-container{
        height: 170px;
        -webkit-justify-content: flex-end;
                justify-content: flex-end;
    }
    .menu-list{
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
    }
    .menu-list > li{
        height: 25px;
        width: 100%;
    }
    .logo{
        color: wheat;
        margin-bottom: 20px;
    }
}

.cartoon-details-container{
    -webkit-align-self: center;
            align-self: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    width: 60%;
}
.cartoon-details-group{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    width: 300px;
    border: 1px solid lightgray;
    padding: 20px 20px 20px 20px;
}
.actors-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: center;
            align-items: center;
}
.actor-name{
    margin: 0 5px 0 0;

}
.add-form-container{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.add-form-group{
    width: 30%;
    min-width: 260px;
    height: 320px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    border: 1px solid lightgray;
}
.add-form{
    margin-top: 20px;
    margin-bottom: 20px;
}
.add-actors-area{
    max-height: 70px;
}
.add-input,
.add-select,
.add-actors-area{
    width: 80%;
    margin-top: 20px;
}
.add-button{
    margin-top: 20px;
}
.validation-error{
    color: tomato;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

